<template>
    <div id="form_editor">
        <button
            type="button"
            class="btn btn-primary btn-sm waves-effect waves-light form_editor_save"
            @click="saveEditor"
        >儲存</button>
        <div class="row form_editor_row">
            <div class="col-3 side_element">
                <ul class="nav nav-tabs mb-0 form_edite_nav">
                    <li class="nav-item" @click="changeContnet('insert')">
                        <a class="nav-link" :class="{ active: contentType == 'insert' }">新增欄位</a>
                    </li>
                    <li
                        class="nav-item"
                        @click="changeContnet('edit')"
                        v-if="defaultEditContents.length > 0"
                    >
                        <a class="nav-link" :class="{ active: contentType == 'edit' }">現有欄位</a>
                    </li>
                </ul>
                <div class="col-block side_element_block">
                    <!-- 新增欄位 -->
                    <div class="block-content" v-if="contentType == 'insert'">
                        <div class="mt-0 md-form md-outline input-with-post-icon search">
                            <input
                                type="search"
                                class="form-control rounded"
                                placeholder="搜尋元件"
                                aria-label="Search"
                                aria-describedby="search-addon"
                                v-model="searchWords"
                            />

                            <i class="fa fa-search input-prefix"></i>
                        </div>
                        <select
                            class="md-form md-outline browser-default custom-select"
                            v-model="insertType"
                        >
                            <option
                                v-for="(type, insertTypeIdx) in insertTypeList"
                                :key="insertTypeIdx"
                                :value="type.value"
                            >{{ type.label }}</option>
                        </select>
                        <!-- 功能區塊 -->
                        <draggable
                            class="dragArea list-group insert-area"
                            :list="filterInsertContents"
                            :group="{ name: 'editor', pull: 'clone', put: false }"
                            :clone="onClone"
                            :sort="false"
                            @change="log"
                            draggable=".addable"
                        >
                            <div
                                class="list-group-item"
                                v-for="(content, filterInsertIdx) in filterInsertContents"
                                :key="filterInsertIdx"
                                :class="{'addable': content.addable != false,'disabled': content.addable == false}"
                            >
                                <div class="list-group-inner">
                                    <div class="group-hd">
                                        <i class="fa fa-cog"></i>
                                        <span>{{ content.label.CH }}</span>
                                    </div>
                                    <span
                                        class="tooltip-area"
                                        data-toggle="tooltip"
                                        :title="content.insertDes"
                                        v-if="content.insertDes"
                                    ></span>
                                </div>
                            </div>
                        </draggable>
                    </div>
                    <!-- 新增欄位 end -->
                    <!-- 現有欄位 -->
                    <div class="block-content" v-else>
                        <div class="mt-0 md-form md-outline input-with-post-icon search">
                            <input
                                type="search"
                                class="form-control rounded"
                                placeholder="搜尋元件"
                                aria-label="Search"
                                aria-describedby="search-addon"
                                v-model="searchWords"
                            />

                            <i class="fa fa-search input-prefix"></i>
                        </div>

                        <!-- 功能區塊 -->
                        <draggable
                            class="dragArea list-group insert-area"
                            :list="filterEditContents"
                            :group="{ name: 'editor', pull: 'clone', put: false }"
                            :clone="onClone"
                            :sort="false"
                            @change="log"
                            draggable=".addable"
                        >
                            <div
                                class="list-group-item"
                                v-for="(content, filterEditIdx) in filterEditContents"
                                :key="filterEditIdx"
                                :class="{'addable': content.addable != false,'disabled': content.addable == false}"
                            >
                                <div class="list-group-inner">
                                    <div class="group-hd">
                                        <i class="fa fa-cog"></i>
                                        <span>{{ content.label.CH }}</span>
                                    </div>
                                    <span
                                        class="tooltip-area"
                                        data-toggle="tooltip"
                                        :title="content.insertDes"
                                        v-if="content.insertDes"
                                    ></span>
                                </div>
                            </div>
                        </draggable>
                    </div>
                    <!-- 現有欄位 end -->
                </div>
            </div>
            <div class="col-6 edit_area">
                <!-- tabs -->
                <ul class="nav nav-tabs mb-0 form_edite_nav">
                    <li
                        class="nav-item"
                        @click="changeI18n(lang.key)"
                        v-for="(lang, langidx) in enumData.langs"
                        :key="langidx"
                    >
                        <a class="nav-link" :class="{ active: i18n == lang.key }">
                            {{
                            lang.label
                            }}
                        </a>
                    </li>
                </ul>
                <!-- tabs -->

                <draggable v-model="defaultContents" @change="log" class="edit-container">
                    <div
                        class="col-block edit_area_block"
                        v-for="(contents, divIndex) in defaultContents"
                        :key="divIndex"
                    >
                        <div class="block-header has-border" @dblclick="editContent(contents)">
                            <h3>
                                <strong>{{ contents.label[i18n] }}</strong>
                                <small
                                    class="form-text text-muted"
                                    v-if="contents.des && contents.des[i18n]"
                                >{{ contents.des[i18n] }}</small>
                            </h3>
                            <button
                                type="button"
                                class="close"
                                aria-label="Close"
                                @click="removeAt(divIndex)"
                            >
                                <span aria-hidden="true">×</span>
                            </button>
                        </div>
                        <div class="block-content">
                            <form>
                                <!-- 編輯區塊 -->
                                <draggable
                                    class="dragArea list-group"
                                    :list="contents.items"
                                    group="editor"
                                    @change="log"
                                    @add="onAdd(divIndex,$event)"
                                >
                                    <div
                                        class="form-group"
                                        v-for="(content, itemIndex) in contents.items"
                                        :key="itemIndex"
                                        :class="{ 'edit-active': content.editorEnable }"
                                        @dblclick="editContent(content, divIndex, itemIndex)"
                                    >
                                        <button
                                            type="button"
                                            class="close"
                                            aria-label="Close"
                                            @click="removeAt(divIndex, itemIndex)"
                                            v-if="content.editorEnable && content.setting.removeable != false"
                                        >
                                            <span aria-hidden="true">×</span>
                                        </button>
                                        <span
                                            class="form-request"
                                            v-if="content.setting && content.setting.require"
                                        >*</span>
                                        <label
                                            :for="`content${divIndex}_${itemIndex}`"
                                            v-if="content.type != 'des' && content.setting.showTitle != false"
                                        >{{ content.label[i18n] }}</label>
                                        <!-- des 說明文字 -->
                                        <div v-if="content.type == 'des'" v-html="content.content"></div>
                                        <!-- des end -->
                                        <!-- text 單行文字 -->
                                        <input
                                            v-else-if="content.type == 'text'"
                                            class="form-control"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :minlength="content.setting.min"
                                            :maxlength="content.setting.max"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        />
                                        <!-- text end -->
                                        <!-- textarea 多行文字 -->
                                        <textarea
                                            v-else-if="content.type == 'textarea'"
                                            class="form-control"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :minlength="content.setting.min"
                                            :maxlength="content.setting.max"
                                            :rows="content.setting.rows"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        ></textarea>
                                        <!-- textarea end -->
                                        <!-- email 電子郵件 -->
                                        <input
                                            v-if="content.type == 'email'"
                                            class="form-control"
                                            type="email"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        />
                                        <!-- email end -->
                                        <!-- number 數字 -->
                                        <input
                                            v-if="content.type == 'number'"
                                            class="form-control"
                                            type="number"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :min="content.setting.min"
                                            :max="content.setting.max"
                                            :step="content.setting.step"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value"
                                        />
                                        <!-- number end -->
                                        <!-- datepicker -->
                                        <div v-else-if="content.type == 'datepicker'" class="row">
                                            <div :class="content.setting.width || 'col-md-12'">
                                                <datepicker
                                                    :value="content.date"
                                                    :placeholder="
                                                      content.setting.placeholder &&
                                                      content.setting.placeholder[i18n]
                                                    "
                                                ></datepicker>
                                            </div>
                                        </div>
                                        <!-- datepicker end -->
                                        <!-- datepicker_age -->
                                        <div
                                            v-else-if="content.type == 'datepicker_age'"
                                            class="row"
                                        >
                                            <div
                                                :class="content.setting.width || 'col-md-12'"
                                                class="datepicker_age"
                                            >
                                                <datepicker
                                                    :value="content.date"
                                                    :placeholder="
                                                      content.setting.placeholder &&
                                                      content.setting.placeholder[i18n]
                                                    "
                                                ></datepicker>
                                                <span>20 歲</span>
                                            </div>
                                        </div>
                                        <!-- datepicker_age end -->

                                        <!-- datetimepicker -->
                                        <div
                                            v-else-if="content.type == 'datetimepicker'"
                                            class="row"
                                        >
                                            <div :class="content.setting.width || 'col-md-12'">
                                                <datetimepicker
                                                    :value="content.datetime"
                                                    :placeholder="
                                                      content.setting.placeholder &&
                                                      content.setting.placeholder[i18n]
                                                    "
                                                ></datetimepicker>
                                            </div>
                                        </div>
                                        <!-- datetimepicker end -->
                                        <!-- select 下拉選項 -->
                                        <select
                                            v-else-if="content.type == 'select'"
                                            class="browser-default custom-select"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                        >
                                            <option
                                                value
                                                disabled
                                                selected
                                                v-if="content.setting.showPlaceholder"
                                            >{{ content.setting.placeholder[i18n] }}</option>
                                            <option
                                                v-for="(option, optionIdx) in content.options"
                                                :key="optionIdx"
                                                :value="option.value"
                                            >{{ option.label }}</option>
                                        </select>
                                        <!-- select end -->
                                        <!-- radio 單選項目 -->
                                        <div
                                            v-else-if="content.type == 'radio'"
                                            :class="content.setting.width || 'col-md-12'"
                                        >
                                            <div
                                                v-for="(option, optionIdx) in content.options"
                                                class="form-check"
                                                :key="optionIdx"
                                                :class="{
                                                  'form-inline': !content.setting.inline,
                                                  'custom-control-inline': content.setting.inline,
                                                  'mb-4': option.des,
                                                }"
                                            >
                                                <input
                                                    type="radio"
                                                    :id="`option_radio_${itemIndex}_${optionIdx}`"
                                                    :name="`radio_${itemIndex}`"
                                                    :value="option.value"
                                                    class="form-check-input"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`option_radio_${itemIndex}_${optionIdx}`"
                                                >{{ option.label }}</label>
                                                <input
                                                    type="text"
                                                    class="form-control form-control-sm ml-2"
                                                    v-if="option.des"
                                                />
                                            </div>
                                        </div>
                                        <!-- radio end -->
                                        <!-- multiple select 多選項 -->
                                        <select
                                            v-else-if="content.type == 'multiple_select'"
                                            class="form-control browser-default"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            multiple
                                        >
                                            <option
                                                value
                                                disabled
                                                selected
                                                v-if="content.setting.showPlaceholder"
                                            >{{ content.setting.placeholder[i18n] }}</option>
                                            <option
                                                v-for="(option, optionIdx) in content.options"
                                                :key="optionIdx"
                                                :value="option.value"
                                            >{{ option.label }}</option>
                                        </select>
                                        <!-- multiple select end -->
                                        <!-- checkbox 複選項目 -->
                                        <div
                                            v-else-if="content.type == 'checkbox'"
                                            :class="content.setting.width || 'col-md-12'"
                                        >
                                            <div
                                                v-for="(option, optionIdx) in content.options"
                                                class="form-check"
                                                :key="optionIdx"
                                                :class="{
                                                  'form-inline': !content.setting.inline,
                                                  'custom-control-inline': content.setting.inline,
                                                  'mb-4': option.des,
                                                }"
                                            >
                                                <input
                                                    type="checkbox"
                                                    :id="`option_checkbox_${itemIndex}_${optionIdx}`"
                                                    :name="`checkbox_${itemIndex}`"
                                                    :value="option.value"
                                                    class="form-check-input"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`option_checkbox_${itemIndex}_${optionIdx}`"
                                                >{{ option.label }}</label>
                                                <input
                                                    type="text"
                                                    class="form-control form-control-sm ml-2"
                                                    v-if="option.des"
                                                />
                                            </div>
                                        </div>
                                        <!-- checkbox end -->

                                        <!-- file upload 檔案上傳 -->
                                        <div v-else-if="content.type == 'file_upload'">
                                            <input
                                                class="form-control-file disable-click"
                                                :accept="`.${content.setting.accept.join(',.')}`"
                                                type="file"
                                            />

                                            <button
                                                type="button"
                                                class="ml-0 btn btn-primary btn-sm waves-effect waves-light"
                                                v-if="content.file"
                                                @click="downloadFile(content.file)"
                                            >
                                                <i class="fa fa-download mr-1"></i> 範本下載
                                            </button>
                                        </div>

                                        <!-- file upload end -->
                                        <!-- file download 檔案下載 -->
                                        <div v-else-if="content.type == 'file_download'">
                                            <small
                                                class="form-text text-muted"
                                                v-if="content.des && content.des[i18n]"
                                            >{{ content.des[i18n] }}</small>
                                            <button
                                                type="button"
                                                class="ml-0 btn btn-primary btn-sm waves-effect waves-light"
                                                @click="downloadFile(content.file)"
                                            >
                                                <i class="fa fa-download mr-1"></i> 檔案下載
                                            </button>
                                        </div>
                                        <!-- file download end -->
                                        <!-- address -->
                                        <div v-else-if="content.type == 'address'" class="form-row">
                                            <div class="col-4 mb-3">
                                                <input
                                                    type="text"
                                                    class="form-control"
                                                    :placeholder="i18n == 'CH' ? `郵遞區號` : `Zip Code`"
                                                />
                                            </div>
                                            <div class="col-8 mb-3">
                                                <select class="browser-default custom-select">
                                                    <option value disabled selected>
                                                        {{
                                                        i18n == "CH"
                                                        ? "請選擇國家"
                                                        : "Please Select Country"
                                                        }}
                                                    </option>
                                                    <option value="1">One</option>
                                                    <option value="2">Two</option>
                                                    <option value="3">Three</option>
                                                </select>
                                            </div>
                                            <div class="col-4">
                                                <input
                                                    type="text"
                                                    class="form-control"
                                                    :placeholder="i18n == 'CH' ? `城市` : `City`"
                                                />
                                            </div>
                                            <div class="col-8">
                                                <input
                                                    type="text"
                                                    class="form-control"
                                                    :placeholder="i18n == 'CH' ? `地址` : `Address`"
                                                />
                                            </div>
                                        </div>
                                        <!-- address end -->
                                        <!-- table -->
                                        <div v-else-if="content.type == 'table'">
                                            <button
                                                v-if="content.setting.addable"
                                                title
                                                data-toggle="tooltip"
                                                type="button"
                                                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                                            >新增</button>
                                            <div class="table-responsive">
                                                <table class="table">
                                                    <thead class="grey lighten-3">
                                                        <tr>
                                                            <th
                                                                style="min-width: 50px; width: 50px;"
                                                                class="text-center"
                                                            >#</th>
                                                            <th
                                                                v-if="content.setting.editable"
                                                                style="min-width: 110px; width: 110px;"
                                                                class="text-center"
                                                            >操作</th>
                                                            <th
                                                                v-for="(column, index) in listDatas[content.tableSn].columns"
                                                                :key="index"
                                                                class="text-center"
                                                            >{{ column.label }}</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr
                                                            v-for="(data, index) in listDatas[content.tableSn].datas"
                                                            :key="index"
                                                        >
                                                            <td>{{ index + 1 }}</td>
                                                            <td
                                                                v-if="content.setting.editable"
                                                                class="text-center"
                                                            >
                                                                <div class="btn-group">
                                                                    <button
                                                                        title
                                                                        data-toggle="tooltip"
                                                                        type="button"
                                                                        class="btn btn-xs btn-default waves-effect waves-light"
                                                                        data-original-title="Edit Client"
                                                                    >
                                                                        <i class="fa fa-pencil"></i>
                                                                    </button>
                                                                    <button
                                                                        title
                                                                        data-toggle="tooltip"
                                                                        type="button"
                                                                        class="btn btn-xs btn-default waves-effect waves-light"
                                                                        data-original-title="Remove Client"
                                                                    >
                                                                        <i class="fa fa-times"></i>
                                                                    </button>
                                                                </div>
                                                            </td>
                                                            <td
                                                                v-for="(item, index) in data"
                                                                :key="index"
                                                                class="text-center"
                                                            >{{ item }}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        <!-- table end -->

                                        <!-- custom -->
                                        <customComponents
                                            type="edit"
                                            :i18n="i18n"
                                            :content="content"
                                            ref="customComponents"
                                        ></customComponents>
                                        <!-- custom end -->
                                        <small
                                            class="form-text text-muted"
                                            v-if="
                                              content.des &&
                                              content.des[i18n] &&
                                              content.setting.showDes != false
                                            "
                                        >{{ content.des[i18n] }}</small>
                                    </div>
                                </draggable>
                            </form>
                        </div>
                    </div>
                </draggable>
                <button
                    type="button"
                    class="btn btn-light-blue btn-lg btn-block waves-effect waves-light mt-20"
                    @click="addDIV"
                >建立 DIV 區塊</button>
            </div>
            <div class="col-3 side_field">
                <div class="col-block side_field_bloxk">
                    <div class="block-header has-border">
                        <h3>
                            <strong>欄位設定</strong>
                        </h3>
                    </div>
                    <div class="block-content edit-area">
                        <!-- 除說明文字外的元件 -->
                        <form
                            v-if="Object.keys(currentEdit).length != 0 && currentEdit.type != 'des'"
                        >
                            <template v-if="currentEdit.setting.showTitle != false">
                                <div
                                    class="form-group"
                                    v-for="(lang, langidx) in enumData.langs"
                                    :key="`標題${langidx}`"
                                >
                                    <label :for="`label_${lang.key}`">標題{{ lang.label }}</label>
                                    <input
                                        v-model="currentEdit.label[lang.key]"
                                        :id="`label_${lang.key}`"
                                        class="form-control form-control-sm"
                                    />
                                </div>
                            </template>
                            <template v-if="currentEdit.setting.showDes != false">
                                <div
                                    class="form-group"
                                    v-for="(lang, langidx) in enumData.langs"
                                    :key="`說明${langidx}`"
                                >
                                    <label :for="`des_${lang.key}`">說明{{ lang.label }}</label>
                                    <textarea
                                        v-model="currentEdit.des[lang.key]"
                                        :id="`des_${lang.key}`"
                                        class="form-control form-control-sm"
                                        rows="3"
                                    ></textarea>
                                </div>
                            </template>
                            <template v-if="!editDiv">
                                <div class="form-group" v-if="currentEdit.type == 'textarea'">
                                    <label for="textarea_rows">行數</label>
                                    <input
                                        v-model="currentEdit.setting.rows"
                                        id="textarea_rows"
                                        class="form-control form-control-sm"
                                    />
                                </div>
                                <template
                                    v-if="
                                      currentEdit.type == 'text' ||
                                      currentEdit.type == 'textarea' ||
                                      currentEdit.type == 'email' ||
                                      currentEdit.type == 'number' ||
                                      currentEdit.type == 'datepicker' ||
                                      currentEdit.type == 'datepicker_age' ||
                                      currentEdit.type == 'datetimepicker'
                                    "
                                >
                                    <!-- 預設日期 -->
                                    <div
                                        class="form-group"
                                        v-if="
                                          currentEdit.type == 'datepicker' ||
                                          currentEdit.type == 'datepicker_age'
                                        "
                                    >
                                        <label for="placeholer">預設資料</label>
                                        <datepicker v-model="currentEdit.date"></datepicker>
                                    </div>
                                    <!-- 預設日期時間 -->
                                    <div
                                        class="form-group"
                                        v-else-if="currentEdit.type == 'datetimepicker'"
                                    >
                                        <label for="placeholer">預設資料</label>
                                        <datetimepicker v-model="currentEdit.datetime"></datetimepicker>
                                    </div>
                                    <!-- 預設數字 -->
                                    <div
                                        class="form-group"
                                        v-else-if="currentEdit.type == 'number'"
                                    >
                                        <label for="placeholer">預設資料</label>
                                        <input
                                            v-model="currentEdit.value"
                                            id="placeholer"
                                            class="form-control form-control-sm"
                                        />
                                    </div>
                                    <!-- 其他預設 -->
                                    <div v-else>
                                        <div
                                            class="form-group"
                                            v-for="(lang, langidx) in enumData.langs"
                                            :key="`預設資料${langidx}`"
                                        >
                                            <label for="value_ch">預設資料{{ lang.label }}</label>
                                            <input
                                                v-model="currentEdit.value[lang.key]"
                                                id="value_ch"
                                                class="form-control form-control-sm"
                                            />
                                        </div>
                                    </div>

                                    <div
                                        class="form-group"
                                        v-for="(lang, langidx) in enumData.langs"
                                        :key="`提示文字${langidx}`"
                                    >
                                        <label for="placeholer_ch">提示文字{{ lang.label }}</label>
                                        <input
                                            v-model="currentEdit.setting.placeholder[lang.key]"
                                            id="placeholer_ch"
                                            class="form-control form-control-sm"
                                        />
                                    </div>
                                </template>

                                <div
                                    class="form-group"
                                    v-if="
                                      currentEdit.type == 'text' ||
