                    tableSn: 0
                }
            ],
            // 現有欄位
            defaultEditContents: [],
            defaultContents: [
                {
                    label: {},
                    items: [
                        {
                            type: "text",
                            label: {},
                            setting: {}
                        }
                    ]
                }
            ],
            confirm: {
                title: "",
                msg: ""
            },
            toastrOptions: {
                closeButton: true,
                debug: false,
                positionClass: "md-toast-top-center",
                onclick: null,
                showDuration: "1000",
                hideDuration: "1000",
                timeOut: "10000",
                extendedTimeOut: "3000",
                showEasing: "swing",
                hideEasing: "linear",
                showMethod: "fadeIn",
                hideMethod: "fadeOut"
            }
        };
    },
    computed: {
        allContents() {
            // 所有 item 移除階層
            let arr = [];
            this.defaultContents.forEach(item => {
                arr = arr.concat(item.items);
            });
            return arr;
        },
        defaultItems() {
            return this.defaultInsertContents.filter(insertItem => {
                return insertItem.insertType == "defaultItem";
            });
        },
        hideDefalutItemInsertContents() {
            // mode dev 才顯示 defaultItems
            return this.defaultInsertContents.filter(insertItem => {
                return this.cbData.dev
                    ? true
                    : insertItem.insertType != "defaultItem";
            });
        },
        checkAddInsertContents() {
            // 是否可新增 新增欄位
            // 除了 基本元件外 其餘頁面中只能有一個 check type && insertType
            return this.hideDefalutItemInsertContents.map(insertItem => {
                insertItem.addable = true;
                this.allContents.forEach(contentItem => {
                    if (
                        contentItem.type == insertItem.type &&
                        insertItem.insertType != "default"
                    ) {
                        insertItem.addable = false;
                    }
                });
                return insertItem;
            });
        },
        filterInsertContents() {
            return this.checkAddInsertContents
                .filter(searchResult =>
                    searchResult.label.CH.match(this.searchWords)
                )
                .filter(typeResult => {
                    if (this.insertType) {
                        return typeResult.insertType == this.insertType;
                    } else {
                        return true;
                    }
                });
        },
        checkAddEditContents() {
            // 是否可新增 現有欄位
            // 頁面中只能有一個 check uuid
            let arr = [];

            this.defaultEditContents.forEach(item => {
                arr = arr.concat(item.items);
            });

            return arr.map(insertItem => {
                insertItem.addable = true;
                this.allContents.forEach(contentItem => {
                    if (contentItem.uuid == insertItem.uuid) {
                        insertItem.addable = false;
                    }
                });
                return insertItem;
            });
        },
        filterEditContents() {
            return this.checkAddEditContents
                .filter(searchResult =>
                    searchResult.label.CH.match(this.searchWords)
                )
                .filter(typeResult => {
                    if (this.insertType) {
                        return typeResult.insertType == this.insertType;
                    } else {
                        return true;
                    }
                });
        },
        rulesTypeArray() {
            // 規則的類型 array
            return this.enumData.rules.map(item => {
                return item.type;
            });
        },
        rulesInType() {
            // 在此類型下的 rules
            return this.enumData.rules.filter(item => {
                return item.type == this.currentEdit.type || item.type == "";
            });
        },
        filterSettings() {
            // 檔案上傳 只顯示 必填欄位
            return this.enumData.settings.filter(setting => {
                return (
                    this.currentEdit.type != "file_upload" ||
                    setting.key == "require" ||
                    setting.key == "invisiable"
                );
            });
        }
    },
    mounted() {
        toastr.options = this.toastrOptions;

        const el = document.querySelector("#cbdata");
        this.cbData = JSON.parse(el.dataset.cbdata);

        // 載入擴充元件
        this.defaultInsertContents = this.defaultInsertContents.concat(
            this.$refs.customComponents[0].defaultInsertContents
        );
        // 取得欄位
        this.getContent();
        // 取得現有欄位
        if (this.cbData.exampleId) {
            this.getEditContent();
        }
    },
    methods: {
        removeAt(divIndex, itemIndex) {
            const thenCB = () => {
                if (itemIndex != undefined) {
                    // 移除 itme
                    this.defaultContents[divIndex].items.splice(itemIndex, 1);
                } else {
                    // 移除 div
                    this.defaultContents.splice(divIndex, 1);
                }
                this.currentEdit = {};
            };
            this.$confirm("提示", "是否確定刪除?", thenCB);
        },
        log(evt) {
            console.log("log", evt);
        },
        onAdd(divIndex, evt) {
            this.closeEdit();
            // 新增item時 增加uuid
            let content = this.defaultContents[divIndex].items[evt.newIndex];
            if (!content.uuid) {
                this.$set(content, "uuid", this.uuid());
            }
            // focus 新增區塊
            this.editContent(content, divIndex, evt.newIndex);
        },
        onClone(evt) {
            // 改寫 clone 為 deep copy 修改值才不會變更原本的
            return { ...evt };
        },
        editContent(content, divIndex, itemIndex) {
            this.closeEdit();
            if (divIndex != undefined) {
                this.editDiv = false;
                this.$set(
                    this.defaultContents[divIndex].items[itemIndex],
                    "editorEnable",
                    true
                );
            } else {
                this.editDiv = true;
            }
            this.currentEdit = content;
        },
        closeEdit() {
            this.defaultContents.forEach((div, divIndex) => {
                this.defaultContents[divIndex].items.forEach(
                    (item, itemIndex) => {
                        this.$set(
                            this.defaultContents[divIndex].items[itemIndex],
                            "editorEnable",
                            false
                        );
                    }
                );
            });
        },
        addDIV() {
            this.defaultContents.push({
                label: { CH: "名稱", EN: "DIV" },
                items: []
            });
        },
        changeI18n(lang) {
            this.i18n = lang;
        },
        changeContnet(type) {
            this.contentType = type;
        },
        addOption() {
            this.currentEdit.options.push({ value: "", label: "" });
        },
        uploadFile(event) {
            this.currentEdit.file = event.target.files[0];
        },
        downloadFile(file) {
            var a = document.createElement("a");
            var url = window.URL.createObjectURL(file);
            var filename = file.name;
            a.href = url;
            a.download = filename;
            a.click();
        },
        checkInline() {
            let des = this.currentEdit.options.some(item => {
                if (item.des) {
                    this.currentEdit.setting.inline = false;
                    return true;
                }
            });
            if (!des && this.currentEdit.setting.inline) {
                this.currentEdit.setting.inline = true;
            }
        },
        saveEditor() {
            let formData = new FormData();

            formData.append("uid", this.cbData.uid);
            formData.append("formId", this.cbData.formId);
            formData.append("data", JSON.stringify(this.defaultContents));

            fetch(this.cbData.api, {
                method: "POST",
                body: formData
            })
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(res => {
                    if (res.success) {
                        toastr["success"]("Success", "儲存成功");
                    } else {
                        toastr["error"]("Error", res.message);
                    }
                });
        },
        uuid() {
            var d = Date.now();
            if (
                typeof performance !== "undefined" &&
                typeof performance.now === "function"
            ) {
                d += performance.now(); //use high-precision timer if available
            }
            return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
                /[xy]/g,
                function(c) {
                    var r = (d + Math.random() * 16) % 16 | 0;
                    d = Math.floor(d / 16);
                    return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
                }
            );
        },
        getContent() {
            fetch(this.cbData.api + `formId/${this.cbData.formId}`)
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(data => {
                    this.defaultContents = data.data;
                    this.enumData = data.enumData;
                });
        },
        getEditContent() {
            fetch(this.cbData.api + `formId/${this.cbData.exampleId}`)
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(data => {
                    this.defaultEditContents = data.data;
                });
        }
    }
};
</script>
