<template>
    <div v-if="type == 'edit'">
        <!-- 編輯區塊 -->
        <!-- batch 梯次選項 -->
        <small
            class="form-text text-muted form-group"
            v-if="content.des && content.des[i18n]"
        >{{ content.des[i18n] }}</small>
        <div v-for="(data, idx) in content.datas" :key="idx" class="form-group">
            <label>{{ data.label }}</label>
            <div>
                <div
                    v-for="(option, idx2) in data.options"
                    class="custom-control form-check custom-control-inline"
                    :key="idx2"
                >
                    <input
                        type="radio"
                        :id="`batch_${idx}_${idx2}`"
                        :value="option.value"
                        class="form-check-input"
                    />
                    <label class="form-check-label">{{ option.label }}</label>
                </div>
            </div>
        </div>

        <!-- batch end -->
    </div>
    <div v-else>
        <!-- 欄位設定 -->
        <!-- 梯次選項 -->
        <button
            data-toggle="tooltip"
            type="button"
            class="ml-0 btn btn-default btn-sm waves-effect waves-light"
            @click="addBatch"
        >新增梯次</button>
        <div class="form-group" v-for="(data, index) in currentEdit.datas" :key="index">
            <table class="table">
                <thead class="grey lighten-3">
                    <tr>
                        <th colspan="3">{{ data.label }}</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(option, index) in data.options" :key="index">
                        <td class="text-center">{{ index + 1 }}</td>
                        <td class="text-center">
                            <div class="btn-group">
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    :disabled="index == 0"
                                    @click="$upOption(data.options, index)"
                                >
                                    <i class="fa fa-chevron-up"></i>
                                </button>
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    :disabled="index == data.options.length - 1"
                                    @click="$downOption(data.options, index)"
                                >
                                    <i class="fa fa-chevron-down"></i>
                                </button>
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    @click="$removeOption(data.options, index)"
                                >
                                    <i class="fa fa-times"></i>
                                </button>
                            </div>
                        </td>
                        <td class="text-center">
                            <input v-model="option.label" class="form-control form-control-sm" />
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- batch modal -->
        <div class="modal fade" id="batchModal">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">報名梯次設定</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                            <label for="batchlabel">梯次名稱</label>
                            <input
                                v-model="batchEdit.label"
                                id="batchlabel"
                                placeholder="請輸入梯次名稱"
                                class="form-control col-md-12"
                            />
                        </div>
                        <div class="form-group">
                            <label for="batchtype">類別名稱，會依相同名稱類別區隔</label>
                            <input
                                v-model="batchEdit.type"
                                id="batchtype"
                                placeholder="請輸入類別名稱"
                                class="form-control col-md-12"
                            />
                        </div>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm btn-default waves-effect waves-light"
                            @click="insertBatch"
                        >儲存</button>
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="closeBatch"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "wish",
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {
            // 梯次
            batchEdit: {
                label: "",
                type: ""
            }
        };
    },
    created() {},
    methods: {
        addBatch() {
            $("#batchModal").modal("show");
        },
        insertBatch() {
            let haveType = this.currentEdit.datas.some(item => {
                if (item.label == this.batchEdit.type) {
                    item.options.push({ label: this.batchEdit.label });
                    return true;
                }
            });
            if (!haveType) {
                this.currentEdit.datas.push({
                    label: this.batchEdit.type,
                    options: [{ label: this.batchEdit.label }]
                });
            }

            this.closeBatch();
        },
        closeBatch() {
            this.batchEdit = {
                label: "",
                type: ""
            };
            $("#batchModal").modal("hide");
        }
    }
};
</script>
